import React from 'react';
import '../styles/tailwind.css';

import { Router } from '@reach/router';

// 
import Layout from '../components/layout';
import AddComicModal from '../components/addComicModal';
import GraphArea from '../clientPagesComponents/graphArea';
import Button from '../components/button';

const App = () => {
  const Default = (props: { path: string }) => <p></p>; // , Defauot 

  const [isModalOpen, setIsModalOpen] = React.useState(false);

  return (
    <Layout>
      <div className='text-right'>
        <Button
          className='md:px-10 md:py-2 md:text-2xl'
          onClick={() => {
            setIsModalOpen(true);
          }}
        >
          
        </Button>
      </div>
      <AddComicModal
        isOpen={isModalOpen}
        onRequestClose={() => {
          setIsModalOpen(false);
        }}
      />

      {/* client-only-routing */}
      <Router basepath='/app'>
        <GraphArea path='/:urlParams' />
        <Default path='/' />
      </Router>
    </Layout>
  );
};

export default App;
